* {
  margin: 0;
  padding: 0;
}

:root {
  --active-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.img_logo {
  width: 40px;
  height: 40px;
  display: flex;
}

.nav_item {
  height: 60px;
  font-size: 12px;
  line-height: 60px;
}

.nav_list {
  text-transform: uppercase;
  list-style: none;
  display: flex;
}

.nav_link {
  color: #000;
  height: 60px;
  margin-left: 20px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.is-active, .nav_link:hover {
  color: var(--active-color);
}

.nav_list .nav_item:first-child .nav_link {
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
